<template>
	<div class="banner">
		<span class="iconfont">&#xe65a;</span>
		<img  :src="banner.imgUrl" :alt="banner.title"/>		
		<p class="title">{{banner.title}}</p>
	</div>
</template>

<script>
	export default{
		props:['banner']
	}
</script>

<style scoped>
	.banner{
		width: 100%;
		position: relative;
	}
	.banner img{
		width: 100%;
		height: 4rem;
		position: absolute;
	}
	.title{
		left: .2rem;
		top: 3.5rem;
		font-size: .36rem;
		z-index: 2;
		color: #ffffff;
		text-shadow: 0 1px 2px rgba(0,0,0,0.70);
		position: absolute;
	}
	.iconfont{
		left: .2rem;
		top: .1rem;
		color: #fff;
		z-index: 2;
		position: absolute;
		width:.6rem ;
		height: .6rem;
		line-height: .6rem;
		text-align: center;
		background: rgba(0,0,0,.5);
		border-radius: 1rem;
	}
</style>


