<template>
	<div class="wrapper"  ref="wrapper">
		<div>
			<!--hot-->
			<div class="hot">
				<h2>热门城市</h2>
				<ul class="hot-list" >
					<router-link to="/home">
						<li class="hot-item" v-for="item of hotCities" :key="item.id" @click="toHome">
							{{item.name}}
						</li>
					</router-link>			
				</ul>
			</div>
			<!--letter-->
			<div class="letter">
			<h2>字母排序</h2>
				<ul class="letter-list" >
					<li class="letter-item" v-for="(item,key) of cities" :key="key" @click="jump(key)">
						{{key}}
					</li>			
				</ul>
			</div>
			<!--list-->
			<div class="list" :ref="key" v-for="(item,key) of cities" :key="key">
				<h2>{{key}}</h2>
					<ul class="list-list">
						<router-link to="/home">
							<li class="list-item" v-for="city of item" :key="city.id" @click="toHome">{{city.name}}</li>
						</router-link>					
					</ul>
				</div>
		</div>
	</div>
	
	
</template>

<script>
	import BScroll from 'better-scroll'
	 export default{
	 	props:['hotCities','cities'],
	 	methods:{
	 		toHome(){
	 			this.$router.push('/')
	 		},
	 		jump(key){
	 			this.scroll.scrollToElement(this.$refs[key][0],2000)
	 		}
	 	},
	 	mounted() {
	      this.$nextTick(() => {
	        //$refs绑定元素
	        if(!this.scroll){
	            this.scroll = new BScroll(this.$refs.wrapper, {
	            //开启点击事件 默认为false
	            click:true
	        })
	        // console.log(this.scroll)
	        }else if(!this.$refs.wrapper){
	            return
	        }
	        else{
	            this.scroll.refresh()
	        }
	      })
	  }
	 }
	 
</script>

<style scoped> 
	.wrapper{
	  width: 100%;
	  position: absolute;
	  top: 45px;
	  bottom: 0;  /*关键*/
	  overflow: hidden;
	  z-index: 1;
	  background: #f5f5f5;
	}	
	h2{
		font-size: .24rem;
		margin: .24rem .3rem;
					
	}
	/*hot*/
	.hot h2{
		padding-top: 7px;
	}
	.hot{
		background: #f5f5f5;
	}
	.hot-list{
		background: #fff;
		color: #212121;
		font-size: .28rem;
		position:relative;
		width: 100%;
		overflow: hidden;
	}
	.hot-item{
		float: left;
		width: 33.3333%;
		text-align: center;
		line-height: .9rem;
		border-bottom: 1px solid #ddd;
		position: relative;
	}
	.hot-list::before{
		content: '';
		position:absolute;
		display: block;
		left: 33.3333%;
		width: 33.333%;
		height: 100%;
		border-left: 1px solid #ddd;
		border-right: 1px solid #ddd;
	}
	/*letter*/
	.letter-list{
		background: #fff;
		color: #212121;
		font-size: .28rem;
		width: 100%;
		overflow: hidden;
	}
	.letter-item{
		width: 16.6666%;
		text-align: center;
		line-height: .9rem;
		float: left;
		
	}
	/*list*/
	.list-list{
		background: #fff;
		color: #212121;
		font-size: .28rem;
		position:relative;
		width: 100%;
		overflow: hidden;
	}
	.list-item{
		float: left;
		width: 25%;
		text-align: center;
		line-height: .9rem;
		border-bottom: 1px solid #ddd;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		position:relative;
	}
	.list-list:before{
		content: '';
		position:absolute;
		display: block;
		left: 25%;
		width: 25%;
		height: 100%;
		border-left: 1px solid #ddd;
		border-right: 1px solid #ddd;
	}
	.list-list:after{
		content: '';
		position:absolute;
		display: block;
		right: 25%;
		width: 0;
		height: 100%;
		border-right: 1px solid #ddd;
	}
</style>


