<template>
	<header>
		<div class="return" @click="returnHome">
			<span class="iconfont">&#xe65a;</span>
		</div>
		<div class="title">城市选择</div>
	</header>
	
	
</template>

<script>
	export default{
		methods:{
			returnHome(){
				this.$router.push('/')
			}
		}
	}
</script>

<style scoped>
	header{
		width: 100%;
		line-height: .88rem;
		background-color: #42B983;
		color: #fff;
		font-size: .32rem;		
	}
	.return{
		padding: 0 .2rem;
		position: absolute;
	}
	.title{
		text-align: center;
	}
</style>



