			</div>	
			<div class="hot-title-right">
				全部榜单<span class="iconfont">&#xe62e;</span>
			</div>
		</div>	
			<div class="top-list">
				<div @click="toDetails(item)" class="top-item" v-for="item of hotList" :key="item.id">
					<img :src="item.imgUrl" :alt="item.title" />
					<p class="mark">{{ item.title }}</p>
					<p class="price"><span>¥{{ item.price }}</span>起</p>		
				</div>
			</div>		
	</div>
</template>

