		data(){
			return{
				hotList:[
					{
						id:'01',
						imgUrl:'http://img1.qunarzz.com/sight/p0/1411/1a/5582f9f24bc50b475a8cbda4c5ac8d24.water.jpg_250x250_c30b25dc.jpg',
						title:'秦始皇陵博物院（兵马俑）',
						price:100
					},
					{
						id:'02',
						imgUrl:'http://img1.qunarzz.com/sight/p0/1411/b8/f5fd5955bdecc923ce6d40ce453160eb.water.jpg_250x250_de106da6.jpg',
						title:'陕西历史博物馆',
						price:30
					},
					{
						id:'03',
						imgUrl:'http://img1.qunarzz.com/sight/p0/1602/53/53cf6caed9f9b9ee90.img.jpg_250x250_96f4c5f8.jpg',
						title:'华清宫',
						price:100
					},
					{
						id:'04',
						imgUrl:'http://img1.qunarzz.com/sight/p0/1703/3a/3a4a9641a8867671a3.img.png_250x250_9da2cdeb.png',
						title:'西安城墙',
						price:54
					},
					{
						id:'05',
						imgUrl:'http://img1.qunarzz.com/sight/p0/1508/db/dbd0bef45fbc36b3.img.jpg_250x250_b81371e3.jpg',
						title:'大唐芙蓉园',
						price:100
					},
					{
						id:'06',
						imgUrl:'http://img1.qunarzz.com/sight/p0/1412/84/f696c67a8052bb60e6fc51b0f9ba7388.water.jpg_250x250_98036577.jpg',
						title:'大明宫国家遗址公园',
						price:82
					},
					{
						id:'07',
						imgUrl:'http://img1.qunarzz.com/sight/p0/1508/c5/a017645d82ab89f271fdfb1bd6ecc2e0.water.jpg_250x250_786e3a24.jpg',
						title:'秦岭野生动物园',
						price:44
					},
					{
						id:'08',
						imgUrl:'http://img1.qunarzz.com/sight/p0/1411/67/272c374a876b2f9ec9860551c993c521.water.jpg_250x250_af5e53bc.jpg',
						title:'西安钟楼',
						price:15
					}
				]
			}
		},
		methods:{
			toDetails(item){
				console.log(item)
				//this.$router.push('/details')
				this.$router.push({
					path:'details',
					query:{
						details:item
					}
				})
			}
		}
	}
</script>

<style scoped>
	.hot{
		background: #fff;
		width: 100%;
	}
	.hot-title{		
		display: flex;
		justify-content: space-between;
		margin-top: .3rem;
		padding: .26rem .2rem ;
	}
	.hot-title-left{
		font-size: .36rem;
	}
	.hot-title-right{
		font-size: .26rem;
		color: #616161;
	}
	.hot-title-left img{
		width: .3rem;
		height: .3rem;
	}
	.top-list{
		font-size: .24rem;
		color:#212121;
		height: 3rem;
		padding-left: .24rem;
		white-space:nowrap;
		overflow-x:scroll;
		overflow-y:none;
	}
	.top-item{
		width: 2rem;
		text-align: center;	
		display:inline-block;
		margin-right: .15rem;
	}
	.top-item p.mark{
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		margin-top: 6px;
	}
	.top-item img{
		width: 100%;
		height: 2rem;
	}
	.price{
		padding: 3px 0 10px;
	}
	.price span{
		color:#ff8300;
	}
</style>


