<template>
	<div class="icons">
		<swiper :options="swiperOption" >
    	<!-- slides -->
    		<swiper-slide >
    				<div class="icons-item" v-for="item of icons1" :key="item.id">
    				<img :src='item.imgUrl' :alt="item.title"/>
    				<p>{{item.title}}</p>
    				</div>
    		</swiper-slide>
    		<swiper-slide >
    				<div class="icons-item" v-for="item of icons2" :key="item.id">
    				<img :src='item.imgUrl' :alt="item.title"/>
    				<p>{{item.title}}</p>
    				</div>
    		</swiper-slide>
		    <!-- Optional controls -->
		    <div class="swiper-pagination"  slot="pagination"></div>
  		</swiper>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				swiperOption:{
					pagination:{
						el:'.swiper-pagination'
					},
				///loop:true
			},
			icons1:[
			{
				id:'01',
				imgUrl:'http://img1.qunarzz.com/piao/fusion/1803/95/f3dd6c383aeb3b02.png',
				title:'景点门票'
			},
			{
				id:'02',
				imgUrl:'http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20193/f0f00d6dfe038c044dbc9a437f58b0eb.png',
				title:'一日行'
			},
			{
				id:'03',
				imgUrl:'http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20194/437aad2dd0c1edb38fee0c51f8f9dd02.png',
				title:'热门演出'
			},
			{
				id:'04',
				imgUrl:'http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20197/d0d101c23a73467de740ecf68602cd17.png',
				title:'壶口瀑布'
			},
			{
				id:'05',
				imgUrl:'http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20198/2a529bd379b0f54da0a97b49e9798a65.png',
				title:'大明宫'
			},
			{
				id:'06',
				imgUrl:'http://img1.qunarzz.com/piao/fusion/1803/76/eb88861d78fb9902.png',
				title:'动植物园'
			},
			{
				id:'07',
				imgUrl:'http://img1.qunarzz.com/piao/fusion/1803/20/831d62d2e1c7be02.png',
				title:'华清宫'
			},
			{
				id:'08',
				imgUrl:'http://img1.qunarzz.com/piao/fusion/1803/fa/2548667cb6e902.png',
				title:'兵马俑'
			}
			],
			icons2:[
				{
				id:'01',
				imgUrl:'http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20196/9c76635f623759e3db287c4850fcb0a5.png',
				title:'华山'
			},
			{
				id:'02',
				imgUrl:'http://img1.qunarzz.com/piao/fusion/1803/20/831d62d2e1c7be02.png',
				title:'博物馆'
			}
			]
			}
		}
	}
</script>

<style scoped>
	.icons{
		background: #fff;
	}
	.icons-item{
		width: 25%;
		padding-top: .2rem;
		float:left;	
	}
	.icons-item img{
		display: block;
		width: 1.1rem;
		height: 1.1rem;
		margin: 0 auto;
	}
	.icons-item p{
		font-size: .28rem;
		text-align: center;
		margin-top: .1rem;
	}
	
	.icons >>> .swiper-container {
        margin-left: auto;
        margin-right: auto;
        position: relative;
        overflow: hidden;
        list-style: none;
        padding: 0;
        z-index: 1;
        height: 4rem;

    }
</style>


