<template>
	<div class="where">
		<div class="where-title">周末去哪儿</div>
		<ul class="where-list">
			<li @click="toDetails(item)" class="where-item" v-for="item of whereList" :key="item.id">
				<img :src="item.imgUrl" :alt="item.title"/>
				<p class="title">{{item.title}}</p>
				<p class="description">{{item.description}}</p>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				whereList:[
					{
						id:'01',
						imgUrl:'http://img1.qunarzz.com/sight/source/1505/19/58f233e11df7ef.jpg_r_640x214_6b42c9fc.jpg',
						title:'西安必打卡',
						description:'寻长安梦，开启穿越的圆梦之旅'
					},
					{
						id:'02',
						imgUrl:'http://img1.qunarzz.com/sight/source/1811/63/808c022b285a2c.jpg_r_640x214_ad984479.jpg',
						title:'探寻文化古都',
						description:'一场时空穿越的旅行，探寻历史悠久的文化古都！寻觅历史留下的点点足迹，细细品味古人的伟大'
					},
					{
						id:'03',
						imgUrl:'http://img1.qunarzz.com/sight/source/1507/de/e6ba44d61894c0.jpg_r_640x214_847324d0.jpg',
						title:'西安演出在线',
						description:'这里有绚丽奇幻的神秘梦幻舞剧，歌舞揭开盛唐面纱，体验穿越时空的奇妙，感受大唐之盛世'
					},
					{
						id:'04',
						imgUrl:'http://img1.qunarzz.com/sight/source/1507/fe/679926851c309a.jpg_r_640x214_114816de.jpg',
						title:'西安亲子必玩',
						description:'西安亲子必去景点，和家人一起度过欢乐亲子时光，带你嗨翻假期！'
					},
					{
						id:'05',
						imgUrl:'http://img1.qunarzz.com/sight/source/1511/14/1cf5dc2ecbfc16.jpg_r_640x214_b4b3a58a.jpg',
						title:'醉美丝路行',
						description:'情系西北，中国梦丝路行'
					}
				]
			}
		},
		methods:{
			toDetails(item){
				console.log(item)
				//this.$router.push('/details')
				this.$router.push({
					path:'details',
					query:{
						details:item
					}
				})
			}
		}
	}
</script>

<style scoped>
	.where{
		color: #212121;
		font-size: .32rem;
	}
	.where-title{
		padding: .26rem;
	}
	.where-list{
		padding-bottom: .1rem;
	}
	.where-item img{
		width: 100%;
		height: 2.8rem;
		
	}
	.title{
		padding:8px 0 0 .26rem;
		background: #fff;
	}
	.description{
		font-size: .24rem;
		color: #616161;
		padding:5px 1.4rem .26rem .26rem;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		background: #fff;
		}
</style>


