<template>
	<div class="like">
		<div class="title">
			<img src="http://img1.qunarzz.com/piao/fusion/1711/89/ebc329f16c55bb02.png" />
		<h2>猜你喜欢</h2>
		</div>
		<ul class="like-list">
				<li @click="toDetails(item)" class="like-item" v-for="item of likeList" :key="item.id">
					<img :src="item.imgUrl" :alt="item.name"/>
					<div class="item-list">
						<p class="list-name">{{item.title}}</p>				
						<p class="comment">
							<span class="comment-star">{{item.star}}</span>
							<span class="comment-num">{{item.num}}条评论</span>
						</p>
						<p class="price">
							<span class="price-like">￥<span class="price-num">{{item.price}}</span></span>起
							<span class="price-address">{{item.address}}</span>
						</p>
						<p class="like-ellipsis">{{item.ellipsis}}</p>
					</div>
				</li>			
		</ul>
	</div>
</template>

