						imgUrl:"http://img1.qunarzz.com/sight/p0/1703/3a/3a4a9641a8867671a3.img.png_200x200_9a235bb9.png",
						title:'西安城墙',
						star:'★★★★★',
						num:'8506',
						price:'54',
						address:'碑林区',
						ellipsis:'触摸历史遗迹，感受历史沧桑'
					},
