<style scoped>
	.like{
		background: #fff;
		margin-top: .3rem;
		padding-top: .3rem;
	}
	.like-list{
		color: #616161;
	}
	.item-list{
		padding-left: .3rem;
	}
	.like-item img{
		padding-left: .2rem;		
	}
	.like-item{
		padding-top: .2rem;
		display: flex;
		border-bottom: .01rem solid #ccc;
	}
