<template>
	<div class="details">
	<!--
		<details-pic></details-pic>
		<details-recom></details-recom>
		<details-ticket></details-ticket>
	-->
		<details-banner :banner="banner"></details-banner>
	</div>
</template>

<script>
	/*import DetailsPic from './pages/Pic'	
	import DetailsRecom from './pages/Recom'	
	import DetailsTicket from './pages/Ticket'	*/
	import DetailsBanner from './pages/Banner'	
	export default{
		components:{
			/*DetailsPic,
			DetailsRecom,
			DetailsTicket,*/
			DetailsBanner
		},
		data(){
			return {
				banner:{}
			}
		},
		created(){
			console.log(this.$route.query.details);
			let data=this.$route.query.details;
			this.banner={
				imgUrl:data.imgUrl,
				title:data.title
			}
		}
	}
</script>

<style scoped>
	.details{
		
	}
</style>


